


































import Vue from "vue";
import Marquee from "./vue/index.vue";

export default Vue.extend({
  components: {
    "v-marquee": Marquee
  },
  methods: {
    pause() {
      (this.$refs.marquee as Marquee).pause();
    },
    start() {
      (this.$refs.marquee as Marquee).start();
    }
  }
});
